import React from "react";
import { useNavigate } from "react-router-dom";
import { useAuth } from "../app/providers/auth-context";
import { Wrench, ShoppingCart, FileText, Package, User, Building2, Settings, Info, LogOut, Database } from "lucide-react";

function Tile({ icon: Icon, label, onClick, testId }) {
  return (
    <button
      onClick={onClick}
      data-testid={testId}
      aria-label={label}
      className="group w-full aspect-square rounded-xl border-2 border-blue-300 bg-white hover:bg-blue-50 transition-colors shadow-sm hover:shadow-md flex items-center justify-center focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-blue-500/40"
    >
      <div className="text-center">
        <div className="mx-auto w-10 h-10 rounded-lg bg-blue-100 text-blue-600 flex items-center justify-center group-hover:bg-blue-600 group-hover:text-white transition-colors">
          <Icon className="w-5 h-5 md:w-6 md:h-6" />
        </div>
        <div className="mt-2 text-slate-700 font-medium text-sm">{label}</div>
      </div>
    </button>
  );
}

export default function MenuDashboard() {
  const { user, logout } = useAuth();
  const navigate = useNavigate();

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-blue-50" data-testid="menu-dashboard">
      {/* Header */}
      <div className="px-5 py-4 border-b border-slate-200 bg-white/80 backdrop-blur-sm sticky top-0 z-40">
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-3">
            <div className="w-10 h-10 rounded-xl bg-blue-600/90 flex items-center justify-center shadow">
              <Wrench className="w-6 h-6 text-white" />
            </div>
            <div>
              <div className="text-lg font-bold text-slate-900" data-testid="dashboard-workshop-name">{user?.workshop_name || "Eka Motor Sport"}</div>
              <div className="text-xs text-slate-500">Selamat datang{user?.username ? `, ${user.username}` : ""}</div>
            </div>
          </div>
          <button onClick={logout} className="p-2 rounded-lg hover:bg-red-50 text-slate-600 hover:text-red-600" title="Keluar" data-testid="logout-button">
            <LogOut className="w-5 h-5" />
          </button>
        </div>
      </div>

      {/* Grid Menu */}
      <div className="container mx-auto px-5 py-6">
        <div className="grid grid-cols-2 sm:grid-cols-3 lg:grid-cols-4 gap-3 sm:gap-4" data-testid="menu-grid">
          <Tile icon={ShoppingCart} label="Kasir" onClick={() => navigate("/kasir")} testId="menu-tile-kasir" />
          <Tile icon={FileText} label="Bon" onClick={() => navigate("/bon")} testId="menu-tile-bon" />
          <Tile icon={Package} label="Produk" onClick={() => navigate("/produk")} testId="menu-tile-produk" />
          <Tile icon={Database} label="Backup" onClick={() => navigate("/backup")} testId="menu-tile-backup" />
          <Tile icon={User} label="Detail Pengguna" onClick={() => navigate("/detail-pengguna")} testId="menu-tile-detail-pengguna" />
          <Tile icon={Building2} label="Detail Bengkel" onClick={() => navigate("/detail-bengkel")} testId="menu-tile-detail-bengkel" />
          <Tile icon={Settings} label="Pengaturan" onClick={() => navigate("/pengaturan")} testId="menu-tile-pengaturan" />
          <Tile icon={Info} label="Tentang Kami" onClick={() => navigate("/tentang-kami")} testId="menu-tile-tentang-kami" />
        </div>
      </div>
    </div>
  );
}