import React, { useEffect, useMemo, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { Button } from "../components/ui/button";
import { Input } from "../components/ui/input";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "../components/ui/card";
import { Badge } from "../components/ui/badge";
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from "../components/ui/dialog";
import { Label } from "../components/ui/label";
import { Plus, User, Wrench, FileText, CreditCard, Phone, Search, X, Users, Clock, CheckCircle, AlertCircle, ChevronRight, RefreshCw, ArrowLeft, Edit3, Trash2, ArrowUpDown, Share2, ClipboardList } from "lucide-react";
import { useToast } from "../hooks/use-toast";
import { API, CacheReader } from "../lib/api";
import { useLoading } from "../hooks/use-loading";
import CustomerDetailView from "../components/customer/CustomerDetailView";

function numberSize(amount) {
  const abs = Math.abs(Number(amount) || 0);
  if (abs >= 1_000_000_000) return "text-sm";
  if (abs >= 1_000_000) return "text-base";
  return "text-lg";
}

function formatIDR(amount) {
  const n = Number(amount || 0);
  const base = new Intl.NumberFormat("id-ID", { maximumFractionDigits: 0, minimumFractionDigits: 0 }).format(n);
  return `${base},-`;
}

export default function Bon() {
  const navigate = useNavigate();
  const params = useParams();
  const [customers, setCustomers] = useState([]);
  const [searchTerm, setSearchTerm] = useState("");
  const [selectedCustomer, setSelectedCustomer] = useState(null);
  const [customerSummary, setCustomerSummary] = useState(null);
  const [showAddCustomer, setShowAddCustomer] = useState(false);
  const [showAddService, setShowAddService] = useState(false);
  const [showAddPayment, setShowAddPayment] = useState(false);
  const [showNewServiceSession, setShowNewServiceSession] = useState(false);
  const [showDeleteConfirm, setShowDeleteConfirm] = useState(false);
  const [showWhatsAppOptions, setShowWhatsAppOptions] = useState(false);
  const [showEditService, setShowEditService] = useState(false);
  const [showDeleteService, setShowDeleteService] = useState(false);
  const [loading, setLoading] = useState(true);
  const { toast } = useToast();

  const [newCustomer, setNewCustomer] = useState({ name: "", phone: "" });
  const [serviceItems, setServiceItems] = useState([{ description: "", price: 0 }]);
  const [newService, setNewService] = useState({ description: "", price: 0, service_session_id: "" });
  const [editingService, setEditingService] = useState(null);
  const [serviceToDelete, setServiceToDelete] = useState(null);
  const [newPayment, setNewPayment] = useState({ amount: 0, description: "", service_session_id: "" });
  const [newServiceSession, setNewServiceSession] = useState({ session_name: "" });
  const [creatingSession, setCreatingSession] = useState(false);
  const { setLoading: setGlobalLoading } = useLoading();
  const [savingPayment, setSavingPayment] = useState(false);

  const withLoading = async (fn) => { try { setGlobalLoading(true); return await fn(); } finally { setGlobalLoading(false); } };

  async function fetchDashboard() {
    try {
      setLoading(true);
      const response = await API.getDashboard({ preferCache: false });
      setCustomers(response.customers);
    } catch (error) {
      toast({ title: " Error", description: error.response?.data?.detail || "Gagal mengambil data bon", variant: "destructive" });
    } finally { setLoading(false); }
  }

  // Initial mount
  useEffect(() => { withLoading(fetchDashboard); }, []);

  // React to dashboard cache updates (prefetch on route change updates cache)
  useEffect(() => {
    const applyCache = () => {
      const d = CacheReader.getDashboard();
      if (d?.customers) setCustomers(d.customers);
    };
    window.addEventListener("cache:dashboard:updated", applyCache);
    window.addEventListener("cache:dashboard:served", applyCache);
    return () => {
      window.removeEventListener("cache:dashboard:updated", applyCache);
      window.removeEventListener("cache:dashboard:served", applyCache);
    };
  }, []);

  // If direct link to /bon/customer/:id
  useEffect(() => {
    const id = params?.customerId;
    if (id) {
      (async () => {
        try {
          await withLoading(async () => {
            let list = customers;
            if (!list.length) {
              const resp = await API.getDashboard({ preferCache: false });
              setCustomers(resp.customers);
              list = resp.customers;
            }
            const match = list.find(c => c.customer.id === id) || null;
            if (match) {
              setSelectedCustomer(match);
              const sum = await API.getCustomerSummary(id, { preferCache: false });
              setCustomerSummary(sum);
            }
          });
        } catch {}
      })();
    } else {
      // When returning to /bon, force refresh dashboard to guarantee latest numbers
      (async () => { try { await withLoading(fetchDashboard); } catch {} })();
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [params?.customerId]);

  const filteredCustomers = useMemo(() => {
    if (!searchTerm.trim()) return customers;
    return customers.filter((c) => c.customer.name.toLowerCase().includes(searchTerm.toLowerCase()) || c.customer.phone.includes(searchTerm));
  }, [customers, searchTerm]);

  function clearSearch() { setSearchTerm(""); }

  async function fetchCustomerSummary(customerId) {
    try {
      await withLoading(async () => {
        const sum = await API.getCustomerSummary(customerId, { preferCache: false });
        setCustomerSummary(sum);
      });
    } catch (error) {
      toast({ title: " Error", description: error?.response?.data?.detail || "Gagal mengambil detail pelanggan", variant: "destructive" });
    }
  }

  // CRUD handlers for detail view
  async function handleAddService(e) {
    e.preventDefault();
    const validItems = serviceItems.filter((i) => i.description.trim() !== "" && i.price > 0);
    if (!validItems.length) return toast({ title: " Error", description: "Harap isi minimal satu item servis", variant: "destructive" });
    try {
      window.dispatchEvent(new CustomEvent("saving-service-item", { detail: true }));
      await Promise.all(validItems.map((item) => API.addService({ ...item, service_session_id: newService.service_session_id, customer_id: selectedCustomer.customer.id })));
      setNewService({ description: "", price: 0, service_session_id: "" }); setServiceItems([{ description: "", price: 0 }]); setShowAddService(false);
      // Setelah update DB, tampilkan overlay global untuk sinkronisasi
      setGlobalLoading(true);
      await fetchCustomerSummary(selectedCustomer.customer.id);
      setGlobalLoading(false);
      toast({ title: " Berhasil!", description: `${validItems.length} item servis berhasil ditambahkan` });
    } catch (error) { toast({ title: " Error", description: error.response?.data?.detail || "Gagal menambahkan service", variant: "destructive" }); }
    finally { window.dispatchEvent(new CustomEvent("saving-service-item", { detail: false })); }
  }

  async function handleEditService(e) {
    e.preventDefault(); if (!editingService) return;
    try { window.dispatchEvent(new CustomEvent("saving-service-item", { detail: true })); await API.updateService(editingService.id, { description: editingService.description, price: editingService.price }); setEditingService(null); setShowEditService(false); await fetchCustomerSummary(selectedCustomer.customer.id); toast({ title: " Berhasil!", description: "Item servis diperbarui" }); }
    catch (error) { toast({ title: " Error", description: error.response?.data?.detail || "Gagal memperbarui item", variant: "destructive" }); }
    finally { window.dispatchEvent(new CustomEvent("saving-service-item", { detail: false })); }
  }

  async function handleDeleteService() {
    if (!serviceToDelete) return;
    try { window.dispatchEvent(new CustomEvent("deleting-service-item", { detail: true })); await API.deleteService(serviceToDelete.id); setServiceToDelete(null); setShowDeleteService(false); await fetchCustomerSummary(selectedCustomer.customer.id); toast({ title: " Berhasil!", description: "Item servis dihapus" }); }
    catch (error) { toast({ title: " Error", description: error.response?.data?.detail || "Gagal menghapus item", variant: "destructive" }); }
    finally { window.dispatchEvent(new CustomEvent("deleting-service-item", { detail: false })); }
  }

  async function handleAddPayment(e) { e.preventDefault(); try { setSavingPayment(true); window.dispatchEvent(new CustomEvent("saving-payment", { detail: true })); const data = { ...newPayment, customer_id: selectedCustomer.customer.id, service_session_id: newPayment.service_session_id || null }; await API.addPayment(data); setNewPayment({ amount: 0, description: "", service_session_id: "" }); setShowAddPayment(false); await fetchCustomerSummary(selectedCustomer.customer.id); toast({ title: " Berhasil!", description: "Pembayaran berhasil dicatat" }); } catch (error) { toast({ title: " Error", description: error.response?.data?.detail || "Gagal mencatat pembayaran", variant: "destructive" }); } finally { setSavingPayment(false); window.dispatchEvent(new CustomEvent("saving-payment", { detail: false })); }}

  async function handleCreateServiceSession(e) { e.preventDefault(); if (creatingSession) return; try { setCreatingSession(true); await API.addServiceSession({ ...newServiceSession, customer_id: selectedCustomer.customer.id }); setNewServiceSession({ session_name: "" }); setShowNewServiceSession(false); await fetchCustomerSummary(selectedCustomer.customer.id); toast({ title: " Berhasil!", description: "Sesi servis baru dibuat" }); } catch (error) { toast({ title: " Error", description: error.response?.data?.detail || "Gagal membuat sesi", variant: "destructive" }); } finally { setCreatingSession(false); } }

  async function handleDeleteCustomer() { await API.deleteCustomer(selectedCustomer.customer.id); setShowDeleteConfirm(false); setSelectedCustomer(null); setCustomerSummary(null); await withLoading(fetchDashboard); toast({ title: " Berhasil!", description: "Data pelanggan dihapus" }); }

  // Detail render
  if (selectedCustomer && customerSummary) {
    return (
      <CustomerDetailView
        customerSummary={customerSummary}
        selectedCustomer={selectedCustomer}
        setSelectedCustomer={setSelectedCustomer}
        setCustomerSummary={setCustomerSummary}
        showWhatsAppOptions={showWhatsAppOptions}
        setShowWhatsAppOptions={setShowWhatsAppOptions}
        showDeleteConfirm={showDeleteConfirm}
        setShowDeleteConfirm={setShowDeleteConfirm}
        showNewServiceSession={showNewServiceSession}
        setShowNewServiceSession={setShowNewServiceSession}
        showAddService={showAddService}
        setShowAddService={setShowAddService}
        showAddPayment={showAddPayment}
        setShowAddPayment={setShowAddPayment}
        showEditService={showEditService}
        setShowEditService={setShowEditService}
        showDeleteService={showDeleteService}
        setShowDeleteService={setShowDeleteService}
        newServiceSession={newServiceSession}
        setNewServiceSession={setNewServiceSession}
        serviceItems={serviceItems}
        setServiceItems={setServiceItems}
        newService={newService}
        setNewService={setNewService}
        newPayment={newPayment}
        setNewPayment={setNewPayment}
        editingService={editingService}
        setEditingService={setEditingService}
        serviceToDelete={serviceToDelete}
        setServiceToDelete={setServiceToDelete}
        handleShareWhatsApp={async (sessionId=null) => { try { const r = await API.getWhatsAppMessage(selectedCustomer.customer.id, sessionId); window.open(r.whatsapp_url, "_blank"); } catch (e) { /* ignore */ } }}
        handleDeleteCustomer={handleDeleteCustomer}
        handleCreateServiceSession={handleCreateServiceSession}
        addServiceItem={() => setServiceItems((p) => [...p, { description: "", price: 0 }])}
        removeServiceItem={(i) => setServiceItems((p) => (p.length > 1 ? p.filter((_, idx) => idx !== i) : p))}
        updateServiceItem={(i, f, v) => setServiceItems((p) => p.map((it, idx) => (idx === i ? { ...it, [f]: v } : it)))}
        handleAddService={handleAddService}
        handleEditService={handleEditService}
        openEditService={(svc) => { setEditingService({ ...svc }); setShowEditService(true); }}
        handleDeleteService={handleDeleteService}
        openDeleteService={(svc) => { setServiceToDelete(svc); setShowDeleteService(true); }}
        handleAddPayment={handleAddPayment}
        fetchCustomerSummary={async (cid) => {
          try { const sum = await API.getCustomerSummary(cid, { preferCache: false }); setCustomerSummary(sum); } catch {}
        }}
        formatCurrency={(a) => formatIDR(a)}
        formatDate={(d) => new Date(d).toLocaleDateString("id-ID", { year: "numeric", month: "long", day: "numeric" })}
        creatingSession={creatingSession}
        onBack={() => { setSelectedCustomer(null); setCustomerSummary(null); navigate("/bon", { replace: true }); }}
      />
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-blue-50 text-[15px] md:text-[16px]">
      <div className="bg-white/80 backdrop-blur-sm border-b border-slate-200 px-5 py-3 sticky top-0 z-40">
        <div className="flex items-center gap-3">
          <button className="p-2 rounded-lg hover:bg-slate-100" onClick={() => navigate("/")} title="Kembali ke Dashboard">
            <ArrowLeft className="w-5 h-5 text-slate-700" />
          </button>
          <div className="w-10 h-10 rounded-xl bg-gradient-to-br from-blue-600 to-indigo-600 flex items-center justify-center shadow-lg">
            <FileText className="w-6 h-6 text-white" />
          </div>
          <div className="text-lg font-semibold text-slate-900">Bon</div>
        </div>
      </div>

      <div className="container mx-auto px-5 md:px-6 py-5 md:py-6">
        {/* Stats */}
        <div className="grid grid-cols-2 md:grid-cols-4 gap-2 md:gap-3">
          <div className="bg-white/60 rounded-lg p-2 md:p-3 border border-slate-200"><div className="flex items-center justify-between"><div><p className="text-[11px] md:text-xs text-slate-600">Total Pelanggan</p><p className={`font-bold text-slate-800 ${numberSize(customers.length)}`}>{customers.length}</p></div><Users className="w-5 h-5 md:w-6 md:h-6 text-blue-600" /></div></div>
          <div className="bg-white/60 rounded-lg p-2 md:p-3 border border-slate-200"><div className="flex items-center justify-between"><div><p className="text-[11px] md:text-xs text-slate-600">Total Hutang</p><p className={`font-bold text-red-600 ${numberSize(customers.reduce((sum, c) => sum + Math.max(0, c.total_debt || 0), 0))}`}>{formatIDR(customers.reduce((sum, c) => sum + Math.max(0, c.total_debt || 0), 0))}</p></div><AlertCircle className="w-5 h-5 md:w-6 md:h-6 text-red-600" /></div></div>
          <div className="bg-white/60 rounded-lg p-2 md:p-3 border border-slate-200"><div className="flex items-center justify-between"><div><p className="text-[11px] md:text-xs text-slate-600">Pelanggan Lunas</p><p className={`font-bold text-green-600 ${numberSize(customers.filter((c) => c.total_debt === 0).length)}`}>{customers.filter((c) => c.total_debt === 0).length}</p></div><CheckCircle className="w-5 h-5 md:w-6 md:h-6 text-green-600" /></div></div>
          <div className="bg-white/60 rounded-lg p-2 md:p-3 border border-slate-200"><div className="flex items-center justify-between"><div><p className="text-[11px] md:text-xs text-slate-600">Ada Hutang</p><p className={`font-bold text-orange-600 ${numberSize(customers.filter((c) => c.total_debt > 0).length)}`}>{customers.filter((c) => c.total_debt > 0).length}</p></div><Clock className="w-5 h-5 md:w-6 md:h-6 text-orange-600" /></div></div>
        </div>

        {/* Search */}
        <div className="mt-6 max-w-md mx-auto lg:max-w-lg">
          <div className="relative">
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 text-slate-400 w-5 h-5" />
            <Input type="text" placeholder="Cari nama pelanggan atau nomor HP..." value={searchTerm} onChange={(e) => setSearchTerm(e.target.value)} className="pl-10 pr-12 py-3 bg-white/80 border-slate-200 text-sm" />
            {searchTerm && (
              <Button variant="ghost" size="sm" onClick={() => setSearchTerm("")} className="absolute right-2 top-1/2 -translate-y-1/2 h-7 w-7 p-0 hover:bg-slate-100"><X className="w-4 h-4" /></Button>
            )}
          </div>
        </div>

        {/* Customers grid */}
        <div className="mt-6 grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4 md:gap-6">
          {filteredCustomers.map((c) => (
            <Card key={c.customer.id} className="cursor-pointer hover:shadow-xl transition-all duration-300 hover:-translate-y-1 bg-white/80 border-0 group" onClick={() => { setSelectedCustomer(c); fetchCustomerSummary(c.customer.id); navigate(`/bon/customer/${c.customer.id}`); }} data-testid={`customer-card-${c.customer.id}`}>
              <CardHeader className="pb-3">
                <div className="flex items-center justify-between">
                  <CardTitle className="text-base md:text-lg group-hover:text-blue-600 transition-colors flex items-center min-w-0">
                    <User className="w-5 h-5 mr-2 shrink-0" />
                    <span className="truncate max-w-[140px] sm:max-w-[180px] md:max-w-[220px]">{c.customer.name}</span>
                  </CardTitle>
                  <Badge variant={c.total_debt > 0 ? "destructive" : c.total_debt < 0 ? "secondary" : "default"} className="shadow-sm text-xs md:text-sm">{c.total_debt > 0 ? " Ada Hutang" : c.total_debt < 0 ? " Kelebihan Bayar" : " Lunas"}</Badge>
                </div>
                <CardDescription className="flex items-center text-sm md:text-base"><Phone className="w-4 h-4 mr-2" />{c.customer.phone}</CardDescription>
              </CardHeader>
              <CardContent>
                <div className="grid grid-cols-2 gap-3 md:gap-4">
                  <div className={`text-center p-3 rounded-lg ${c.total_debt > 0 ? 'bg-gradient-to-br from-red-50 to-red-100' : c.total_debt < 0 ? 'bg-gradient-to-br from-blue-50 to-blue-100' : 'bg-gradient-to-br from-green-50 to-green-100'}`}>
                    <div className={`font-bold ${c.total_debt > 0 ? 'text-red-600' : c.total_debt < 0 ? 'text-blue-600' : 'text-green-600'} ${numberSize(c.total_debt < 0 ? Math.abs(c.total_debt) : c.total_debt)}`}>{c.total_debt < 0 ? formatIDR(Math.abs(c.total_debt)) : formatIDR(c.total_debt)}</div>
                    <div className="text-[11px] md:text-xs text-slate-600">{c.total_debt < 0 ? 'Sisa Uang' : 'Sisa Hutang'}</div>
                  </div>
                  <div className="text-center p-3 bg-gradient-to-br from-blue-50 to-blue-100 rounded-lg">
                    <div className={`font-bold text-blue-600 ${numberSize(c.total_service_sessions)}`}>{c.total_service_sessions}</div>
                    <div className="text-[11px] md:text-xs text-slate-600">Sesi Servis</div>
                  </div>
                </div>
                <div className="mt-3 flex items-center justify-end text-blue-600 group-hover:text-blue-700"><span className="text-xs md:text-sm font-medium">Lihat Detail</span><ChevronRight className="w-4 h-4 ml-1 group-hover:translate-x-1 transition-transform" /></div>
              </CardContent>
            </Card>
          ))}
        </div>

        {filteredCustomers.length === 0 && customers.length > 0 && searchTerm && (
          <div className="text-center py-16"><Search className="mx-auto w-16 h-16 text-slate-400 mb-4" /><h3 className="text-lg md:text-xl font-semibold text-slate-600 mb-2">Tidak ada hasil</h3><p className="text-slate-500 mb-6">Tidak ditemukan pelanggan dengan kata "{searchTerm}"</p><Button variant="outline" onClick={clearSearch} className="hover:bg-blue-50"><X className="w-4 h-4 mr-2" />Hapus Pencarian</Button></div>
        )}

        {/* Floating add customer */}
        <Dialog open={showAddCustomer} onOpenChange={setShowAddCustomer}>
          <DialogTrigger asChild>
            <Button className="fixed bottom-6 right-6 w-12 h-12 md:w-14 md:h-14 rounded-full bg-blue-600 hover:bg-blue-700 shadow-2xl z-50 p-0 flex items-center justify-center"><Plus className="w-5 h-5 md:w-6 md:h-6 text-white" /></Button>
          </DialogTrigger>
          <DialogContent>
            <DialogHeader><DialogTitle>Tambah Pelanggan Baru</DialogTitle><DialogDescription>Tambahkan pelanggan baru ke Menu Bon</DialogDescription></DialogHeader>
            <form onSubmit={async (e) => { e.preventDefault(); try { await withLoading(async () => { await API.addCustomer(newCustomer); }); setNewCustomer({ name: "", phone: "" }); setShowAddCustomer(false); await withLoading(fetchDashboard); } catch (error) { toast({ title: " Error", description: error.response?.data?.detail || "Gagal menambahkan pelanggan", variant: "destructive" }); } }} className="space-y-4">
              <div><Label htmlFor="name">Nama Pelanggan</Label><Input id="name" placeholder="Masukkan nama" value={newCustomer.name} onChange={(e) => setNewCustomer({ ...newCustomer, name: e.target.value })} required /></div>
              <div><Label htmlFor="phone">No HP/WhatsApp</Label><Input id="phone" placeholder="Contoh: 08123456789" value={newCustomer.phone} onChange={(e) => setNewCustomer({ ...newCustomer, phone: e.target.value })} required /></div>
              <Button type="submit" className="w-full"><Plus className="w-4 h-4 mr-2" />Tambah Pelanggan</Button>
            </form>
          </DialogContent>
        </Dialog>
      </div>
    </div>
  );
}