import React, { useEffect, useMemo, useState } from "react";
import { useNavigate } from "react-router-dom";
import { ArrowLeft, Package, Plus, Trash2, Edit3, RefreshCw } from "lucide-react";
import { API } from "../lib/api";
import { Button } from "../components/ui/button";
import { Input } from "../components/ui/input";
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle } from "../components/ui/dialog";

function formatIDR(n) {
  const v = Number(n || 0);
  return new Intl.NumberFormat("id-ID", { maximumFractionDigits: 0, minimumFractionDigits: 0 }).format(v);
}

export default function Products() {
  const navigate = useNavigate();
  const [items, setItems] = useState([]);
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [editing, setEditing] = useState(null); // product object
  const [filter, setFilter] = useState("");
  const [showForm, setShowForm] = useState(false);
  const [reloading, setReloading] = useState(false);

  const emptyForm = { code: "", name: "", stock: 0, unit: "pcs", cost_price: 0, sale_price: 0, workshop_price: 0, note: "" };
  const [form, setForm] = useState(emptyForm);

  async function refresh() {
    setLoading(true);
    try {
      const { products } = await API.listProducts();
      setItems(products || []);
    } finally {
      setLoading(false);
    }
  }

  useEffect(() => { refresh(); }, []);

  const filtered = useMemo(() => {
    const q = filter.trim().toLowerCase();
    if (!q) return items;
    return items.filter(p => p.name.toLowerCase().includes(q) || p.code.toLowerCase().includes(q));
  }, [items, filter]);

  async function handleSubmit(e) {
    e.preventDefault();
    setSaving(true);
    try {
      if (editing) {
        await API.updateProduct(editing.id, form);
      } else {
        await API.createProduct(form);
      }
      setForm(emptyForm);
      setEditing(null);
      setShowForm(false);
      await refresh();
    } finally { setSaving(false); }
  }

  function openCreate() {
    setEditing(null);
    setForm(emptyForm);
    setShowForm(true);
  }

  async function startEdit(p) {
    setEditing(p);
    setForm({ code: p.code, name: p.name, stock: p.stock, unit: p.unit, cost_price: p.cost_price, sale_price: p.sale_price, workshop_price: p.workshop_price, note: p.note || "" });
    setShowForm(true);
  }

  async function handleDelete(id) {
    if (!window.confirm("Hapus item ini?")) return;
    await API.deleteProduct(id);
    await refresh();
  }

  async function onReloadClick() {
    setReloading(true);
    try { await refresh(); } finally { setReloading(false); }
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-blue-50">
      <div className="bg-white/80 backdrop-blur-sm border-b border-slate-200 px-5 py-3 sticky top-0 z-40 flex items-center gap-3">
        <button className="p-2 rounded-lg hover:bg-slate-100" onClick={() => navigate("/")} title="Kembali ke Dashboard">
          <ArrowLeft className="w-5 h-5 text-slate-700" />
        </button>
        <div className="w-10 h-10 rounded-xl bg-gradient-to-br from-blue-600 to-indigo-600 flex items-center justify-center shadow-lg">
          <Package className="w-6 h-6 text-white" />
        </div>
        <div className="text-lg font-semibold text-slate-900">Produk</div>
      </div>

      <div className="container mx-auto px-5 py-6">
        {/* Toolbar: Search + Add (+) + Reload spinner */}
        <div className="mb-4 flex items-center gap-2">
          <Input placeholder="Cari kode atau nama..." value={filter} onChange={(e) => setFilter(e.target.value)} className="max-w-sm" />
          <Button onClick={openCreate} className="h-10 w-10 p-0 flex items-center justify-center" title="Tambah produk"><Plus className="w-5 h-5" /></Button>
          <Button variant="outline" onClick={onReloadClick} disabled={reloading} className="h-10 w-10 p-0 flex items-center justify-center" title="Muat ulang">
            <RefreshCw className={`w-5 h-5 ${reloading ? 'animate-spin' : ''}`} />
          </Button>
        </div>

        {/* Tabel */}
        <div className="overflow-auto bg-white rounded-xl border border-slate-200">
          <table className="min-w-full text-sm">
            <thead className="bg-slate-50 text-slate-700">
              <tr>
                <th className="text-left p-3">Kode</th>
                <th className="text-left p-3 min-w-[180px]">Nama</th>
                <th className="text-right p-3">Stok</th>
                <th className="text-left p-3">Satuan</th>
                <th className="text-right p-3">Bengkel</th>
                <th className="text-right p-3">Jual</th>
                <th className="text-right p-3">Modal</th>
                <th className="text-left p-3">Ket</th>
                <th className="text-right p-3">Aksi</th>
              </tr>
            </thead>
            <tbody>
              {loading ? (
                <tr><td colSpan="9" className="p-6 text-center text-slate-500">Memuat data...</td></tr>
              ) : filtered.length === 0 ? (
                <tr><td colSpan="9" className="p-6 text-center text-slate-500">Belum ada data</td></tr>
              ) : (
                filtered.map((p) => (
                  <tr key={p.id} className="border-t hover:bg-slate-50">
                    <td className="p-3 font-mono text-xs">{p.code}</td>
                    <td className="p-3 min-w-[180px]">{p.name}</td>
                    <td className="p-3 text-right">{p.stock}</td>
                    <td className="p-3">{p.unit}</td>
                    <td className="p-3 text-right">{formatIDR(p.workshop_price)}</td>
                    <td className="p-3 text-right">{formatIDR(p.sale_price)}</td>
                    <td className="p-3 text-right">{formatIDR(p.cost_price)}</td>
                    <td className="p-3 max-w-[240px] truncate" title={p.note}>{p.note}</td>
                    <td className="p-3 text-right">
                      <div className="inline-flex items-center gap-1">
                        <Button size="sm" variant="outline" onClick={() => startEdit(p)} className="h-8 w-8 p-0" title="Edit"><Edit3 className="w-3 h-3" /></Button>
                        <Button size="sm" variant="outline" onClick={() => handleDelete(p.id)} className="h-8 w-8 p-0 hover:bg-red-50 hover:border-red-200" title="Hapus"><Trash2 className="w-3 h-3 text-red-600" /></Button>
                      </div>
                    </td>
                  </tr>
                ))
              )}
            </tbody>
          </table>
        </div>
      </div>

      {/* Dialog Tambah/Ubah Produk */}
      <Dialog open={showForm} onOpenChange={(v) => { if (!v) { setShowForm(false); setEditing(null); } }}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>{editing ? "Ubah Produk" : "Tambah Produk"}</DialogTitle>
            <DialogDescription>{editing ? "Perbarui detail produk" : "Masukkan detail produk baru"}</DialogDescription>
          </DialogHeader>
          <form onSubmit={handleSubmit} className="grid grid-cols-1 md:grid-cols-12 gap-3">
            <div className="md:col-span-3">
              <label className="text-xs text-slate-500 block mb-1">Kode Item/Barang</label>
              <Input placeholder="Contoh: BRG001" value={form.code} onChange={(e) => setForm({ ...form, code: e.target.value })} required />
            </div>
            <div className="md:col-span-4">
              <label className="text-xs text-slate-500 block mb-1">Nama Item/Barang</label>
              <Input placeholder="Contoh: Oli Mesin 10W-40" value={form.name} onChange={(e) => setForm({ ...form, name: e.target.value })} required />
            </div>
            <div className="md:col-span-2">
              <label className="text-xs text-slate-500 block mb-1">Stok</label>
              <Input type="number" placeholder="0" value={form.stock} onChange={(e) => setForm({ ...form, stock: parseInt(e.target.value||0,10) })} required />
            </div>
            <div className="md:col-span-3">
              <label className="text-xs text-slate-500 block mb-1">Satuan</label>
              <select className="h-10 w-full px-3 border border-slate-200 rounded-lg" value={form.unit} onChange={(e) => setForm({ ...form, unit: e.target.value })}>
                <option value="pcs">pcs</option>
                <option value="dus">dus</option>
                <option value="set">set</option>
              </select>
            </div>
            <div className="md:col-span-4">
              <label className="text-xs text-slate-500 block mb-1">Harga Pokok/Modal</label>
              <Input type="number" placeholder="0" value={form.cost_price} onChange={(e) => setForm({ ...form, cost_price: parseFloat(e.target.value||0) })} />
            </div>
            <div className="md:col-span-4">
              <label className="text-xs text-slate-500 block mb-1">Harga Jual</label>
              <Input type="number" placeholder="0" value={form.sale_price} onChange={(e) => setForm({ ...form, sale_price: parseFloat(e.target.value||0) })} />
            </div>
            <div className="md:col-span-4">
              <label className="text-xs text-slate-500 block mb-1">Harga Bengkel</label>
              <Input type="number" placeholder="0" value={form.workshop_price} onChange={(e) => setForm({ ...form, workshop_price: parseFloat(e.target.value||0) })} />
            </div>
            <div className="md:col-span-12">
              <label className="text-xs text-slate-500 block mb-1">Keterangan</label>
              <Input placeholder="Opsional" value={form.note} onChange={(e) => setForm({ ...form, note: e.target.value })} />
            </div>
            <div className="md:col-span-12 flex items-center gap-2 justify-end">
              <Button type="button" variant="outline" onClick={() => { setShowForm(false); setEditing(null); }}>Batal</Button>
              <Button type="submit" disabled={saving}>
                {saving ? (<span className="inline-flex items-center"><RefreshCw className="w-4 h-4 mr-2 animate-spin" />Menyimpan...</span>) : (<span className="inline-flex items-center"><Plus className="w-4 h-4 mr-2" />{editing ? "Simpan Perubahan" : "Tambah Produk"}</span>)}
              </Button>
            </div>
          </form>
        </DialogContent>
      </Dialog>
    </div>
  );
}