import React, { useEffect, useRef } from "react";
import { useLocation } from "react-router-dom";
import { useLoading } from "../../hooks/use-loading";
import { syncForLocation } from "./routeSyncRegistry";

export default function GlobalRouteEffects() {
  const location = useLocation();
  const { setLoading } = useLoading();
  const abortRef = useRef({ aborted: false });

  useEffect(() => {
    abortRef.current.aborted = true;
    abortRef.current = { aborted: false };
    const currentToken = abortRef.current;

    async function run() {
      setLoading(true);
      const MIN_SHOW_MS = 650; // keep a bit longer to ensure data-bound UI is ready
      const start = Date.now();
      try {
        await syncForLocation(location.pathname, location.search);
      } catch {
        // ignore
      } finally {
        const elapsed = Date.now() - start;
        const waitMore = Math.max(0, MIN_SHOW_MS - elapsed);
        setTimeout(() => { if (!currentToken.aborted) setLoading(false); }, waitMore);
      }
    }

    run();
    return () => { currentToken.aborted = true; };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [location.pathname, location.search]);

  return null;
}