import React, { useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import { ArrowLeft, Database, RefreshCw, Download, Upload, FileSpreadsheet } from "lucide-react";
import { Button } from "../components/ui/button";
import { API } from "../lib/api";
import * as XLSX from 'xlsx';

export default function Backup() {
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);
  const fileInputRef = useRef(null);

  async function downloadJson(filename, data) {
    const blob = new Blob([JSON.stringify(data, null, 2)], { type: "application/json" });
    const url = URL.createObjectURL(blob);
    const a = document.createElement("a");
    a.href = url; a.download = filename; a.click();
    URL.revokeObjectURL(url);
  }

  // --- Products-only ---
  async function exportProducts() {
    try { setLoading(true); const { data } = await API._raw.get("/backup/products"); await downloadJson("backup_products.json", data); } finally { setLoading(false); }
  }
  async function importProducts(file) {
    if (!file) return;
    const text = await file.text();
    const json = JSON.parse(text || "{}");
    const count = Array.isArray(json?.products) ? json.products.length : 0;
    if (!window.confirm(`Anda akan menghapus semua produk saat ini dan mengimpor ${count} produk baru. Lanjutkan?`)) return;
    try { setLoading(true); await API._raw.post("/backup/products", json); alert("Restore produk berhasil (" + count + " item)"); } catch { alert("Restore produk gagal"); } finally { setLoading(false); }
  }

  // --- All data ---
  async function exportAll() {
    try { setLoading(true); const { data } = await API._raw.get("/backup/all"); await downloadJson("backup_all.json", data); } finally { setLoading(false); }
  }
  async function importAll(file) {
    if (!file) return;
    const text = await file.text();
    const json = JSON.parse(text || "{}");
    const counts = {
      customers: (json?.customers || []).length,
      service_sessions: (json?.service_sessions || []).length,
      services: (json?.services || []).length,
      payments: (json?.payments || []).length,
      products: (json?.products || []).length,
    };
    const msg = `Anda akan MENGHAPUS SEMUA DATA saat ini dan mengimpor data baru:\n - Pelanggan: ${counts.customers}\n - Sesi: ${counts.service_sessions}\n - Servis: ${counts.services}\n - Pembayaran: ${counts.payments}\n - Produk: ${counts.products}\nLanjutkan?`;
    if (!window.confirm(msg)) return;
    try { setLoading(true); await API._raw.post("/backup/all", json); alert("Restore semua data berhasil"); } catch { alert("Restore semua data gagal"); } finally { setLoading(false); }
  }

  function openFilePicker(cb, accept = ".json,application/json") {
    const input = document.createElement("input");
    input.type = "file"; input.accept = accept; input.onchange = (e) => cb(e.target.files?.[0] || null); input.click();
  }

  // --- Excel Products ---
  async function exportProductsExcel() {
    try {
      setLoading(true);
      const response = await fetch(`${process.env.REACT_APP_BACKEND_URL}/api/backup/excel/products`, {
        headers: { Authorization: `Bearer ${localStorage.getItem("token")}` }
      });
      if (!response.ok) throw new Error("Failed to download");
      const blob = await response.blob();
      const url = URL.createObjectURL(blob);
      const a = document.createElement("a");
      a.href = url; a.download = "backup_produk.xlsx"; a.click();
      URL.revokeObjectURL(url);
    } finally { setLoading(false); }
  }

  async function importProductsExcel(file) {
    if (!file) return;
    if (!file.name.endsWith('.xlsx') && !file.name.endsWith('.xls')) {
      alert("File harus berformat Excel (.xlsx atau .xls)");
      return;
    }
    
    try {
      // Preview data count
      const data = await file.arrayBuffer();
      const workbook = XLSX.read(data);
      const worksheet = workbook.Sheets['Produk'];
      const jsonData = XLSX.utils.sheet_to_json(worksheet);
      
      if (!window.confirm(`Anda akan menghapus semua produk saat ini dan mengimpor ${jsonData.length} produk dari Excel. Lanjutkan?`)) return;
      
      setLoading(true);
      const formData = new FormData();
      formData.append('file', file);
      
      const response = await fetch(`${process.env.REACT_APP_BACKEND_URL}/api/backup/excel/products`, {
        method: 'POST',
        headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
        body: formData
      });
      
      if (!response.ok) throw new Error("Upload failed");
      const result = await response.json();
      alert(`Restore produk berhasil (${result.inserted} item)`);
    } catch (error) {
      alert(`Restore produk gagal: ${error.message}`);
    } finally { setLoading(false); }
  }

  // --- Excel All Data ---
  async function exportAllExcel() {
    try {
      setLoading(true);
      const response = await fetch(`${process.env.REACT_APP_BACKEND_URL}/api/backup/excel/all`, {
        headers: { Authorization: `Bearer ${localStorage.getItem("token")}` }
      });
      if (!response.ok) throw new Error("Failed to download");
      const blob = await response.blob();
      const url = URL.createObjectURL(blob);
      const a = document.createElement("a");
      a.href = url; a.download = "backup_semua_data.xlsx"; a.click();
      URL.revokeObjectURL(url);
    } finally { setLoading(false); }
  }

  async function importAllExcel(file) {
    if (!file) return;
    if (!file.name.endsWith('.xlsx') && !file.name.endsWith('.xls')) {
      alert("File harus berformat Excel (.xlsx atau .xls)");
      return;
    }
    
    try {
      // Preview data counts
      const data = await file.arrayBuffer();
      const workbook = XLSX.read(data);
      const counts = {
        customers: workbook.Sheets['Pelanggan'] ? XLSX.utils.sheet_to_json(workbook.Sheets['Pelanggan']).length : 0,
        sessions: workbook.Sheets['Sesi Servis'] ? XLSX.utils.sheet_to_json(workbook.Sheets['Sesi Servis']).length : 0,
        services: workbook.Sheets['Item Servis'] ? XLSX.utils.sheet_to_json(workbook.Sheets['Item Servis']).length : 0,
        payments: workbook.Sheets['Pembayaran'] ? XLSX.utils.sheet_to_json(workbook.Sheets['Pembayaran']).length : 0,
        products: workbook.Sheets['Produk'] ? XLSX.utils.sheet_to_json(workbook.Sheets['Produk']).length : 0,
      };
      
      const msg = `Anda akan MENGHAPUS SEMUA DATA saat ini dan mengimpor data baru dari Excel:\n - Pelanggan: ${counts.customers}\n - Sesi: ${counts.sessions}\n - Servis: ${counts.services}\n - Pembayaran: ${counts.payments}\n - Produk: ${counts.products}\nLanjutkan?`;
      if (!window.confirm(msg)) return;
      
      setLoading(true);
      const formData = new FormData();
      formData.append('file', file);
      
      const response = await fetch(`${process.env.REACT_APP_BACKEND_URL}/api/backup/excel/all`, {
        method: 'POST',
        headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
        body: formData
      });
      
      if (!response.ok) throw new Error("Upload failed");
      const result = await response.json();
      alert("Restore semua data berhasil");
    } catch (error) {
      alert(`Restore semua data gagal: ${error.message}`);
    } finally { setLoading(false); }
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-blue-50">
      <div className="bg-white/80 backdrop-blur-sm border-b border-slate-200 px-5 py-3 sticky top-0 z-40 flex items-center gap-3">
        <button className="p-2 rounded-lg hover:bg-slate-100" onClick={() => navigate("/")} title="Kembali ke Dashboard">
          <ArrowLeft className="w-5 h-5 text-slate-700" />
        </button>
        <div className="w-10 h-10 rounded-xl bg-gradient-to-br from-blue-600 to-indigo-600 flex items-center justify-center shadow-lg">
          <Database className="w-6 h-6 text-white" />
        </div>
        <div className="text-lg font-semibold text-slate-900">Backup</div>
      </div>

      <div className="container mx-auto px-5 py-8 text-slate-700 space-y-6">
        {/* Excel Backup/Restore */}
        <section className="max-w-3xl bg-gradient-to-r from-green-50 to-green-100 rounded-xl border border-green-200 p-6">
          <div className="flex items-center mb-4">
            <FileSpreadsheet className="w-6 h-6 text-green-600 mr-3" />
            <h2 className="text-xl font-semibold text-green-800"> Backup/Restore Excel (Direkomendasikan)</h2>
          </div>
          <p className="text-sm text-green-700 mb-6">Format Excel memungkinkan Anda membaca dan mengedit data dengan mudah menggunakan Microsoft Excel, Google Sheets, atau aplikasi spreadsheet lainnya.</p>
          
          <div className="grid md:grid-cols-2 gap-6">
            <div className="bg-white rounded-lg p-5 border border-green-200">
              <h3 className="font-semibold text-slate-900 mb-2">1) Data Produk Excel</h3>
              <p className="text-sm text-slate-600 mb-4">Backup/restore hanya data produk dalam format Excel yang mudah diedit.</p>
              <div className="flex flex-wrap items-center gap-2">
                <Button onClick={exportProductsExcel} disabled={loading} className="bg-green-600 hover:bg-green-700 inline-flex items-center">
                  <FileSpreadsheet className="w-4 h-4 mr-2" />Backup Excel
                </Button>
                <Button variant="outline" onClick={() => openFilePicker(importProductsExcel, ".xlsx,.xls")} disabled={loading} className="border-green-300 hover:bg-green-50 inline-flex items-center">
                  <Upload className="w-4 h-4 mr-2" />Restore Excel
                </Button>
                {loading && <RefreshCw className="w-4 h-4 animate-spin text-slate-500" />}
              </div>
            </div>

            <div className="bg-white rounded-lg p-5 border border-green-200">
              <h3 className="font-semibold text-slate-900 mb-2">2) Semua Data Excel</h3>
              <p className="text-sm text-slate-600 mb-4">Backup/restore semua data bengkel dalam format Excel dengan multiple sheets.</p>
              <div className="flex flex-wrap items-center gap-2">
                <Button onClick={exportAllExcel} disabled={loading} className="bg-green-600 hover:bg-green-700 inline-flex items-center">
                  <FileSpreadsheet className="w-4 h-4 mr-2" />Backup Excel
                </Button>
                <Button variant="outline" onClick={() => openFilePicker(importAllExcel, ".xlsx,.xls")} disabled={loading} className="border-green-300 hover:bg-green-50 inline-flex items-center">
                  <Upload className="w-4 h-4 mr-2" />Restore Excel
                </Button>
                {loading && <RefreshCw className="w-4 h-4 animate-spin text-slate-500" />}
              </div>
            </div>
          </div>
        </section>

        {/* JSON Backup/Restore - Legacy */}
        <section className="max-w-3xl bg-white rounded-xl border border-slate-200 p-6">
          <div className="flex items-center mb-4">
            <Database className="w-6 h-6 text-slate-600 mr-3" />
            <h2 className="text-xl font-semibold text-slate-900"> Backup/Restore JSON (Legacy)</h2>
          </div>
          <p className="text-sm text-slate-600 mb-6">Format JSON untuk kompatibilitas dengan sistem lama atau integrasi khusus.</p>
          
          <div className="grid md:grid-cols-2 gap-6">
            <div className="bg-slate-50 rounded-lg p-5 border border-slate-200">
              <h3 className="font-semibold text-slate-900 mb-2">1) Data Produk JSON</h3>
              <p className="text-sm text-slate-600 mb-4">Cadangkan atau pulihkan hanya data produk/sparepart.</p>
              <div className="flex flex-wrap items-center gap-2">
                <Button onClick={exportProducts} disabled={loading} variant="outline" className="inline-flex items-center">
                  <Download className="w-4 h-4 mr-2" />Backup JSON
                </Button>
                <Button variant="outline" onClick={() => openFilePicker(importProducts)} disabled={loading} className="inline-flex items-center">
                  <Upload className="w-4 h-4 mr-2" />Restore JSON
                </Button>
                {loading && <RefreshCw className="w-4 h-4 animate-spin text-slate-500" />}
              </div>
            </div>

            <div className="bg-slate-50 rounded-lg p-5 border border-slate-200">
              <h3 className="font-semibold text-slate-900 mb-2">2) Semua Data JSON</h3>
              <p className="text-sm text-slate-600 mb-4">Cadangkan atau pulihkan semua data yang terkait akun bengkel.</p>
              <div className="flex flex-wrap items-center gap-2">
                <Button onClick={exportAll} disabled={loading} variant="outline" className="inline-flex items-center">
                  <Download className="w-4 h-4 mr-2" />Backup JSON
                </Button>
                <Button variant="outline" onClick={() => openFilePicker(importAll)} disabled={loading} className="inline-flex items-center">
                  <Upload className="w-4 h-4 mr-2" />Restore JSON
                </Button>
                {loading && <RefreshCw className="w-4 h-4 animate-spin text-slate-500" />}
              </div>
            </div>
          </div>
        </section>

        {/* Info Section */}
        <section className="max-w-3xl bg-blue-50 rounded-xl border border-blue-200 p-6">
          <h3 className="font-semibold text-blue-800 mb-3"> Tips Penggunaan Excel Backup</h3>
          <div className="space-y-2 text-sm text-blue-700">
            <p> <strong>Backup Excel</strong> menghasilkan file dengan multiple sheets yang terorganisir (Pelanggan, Sesi Servis, Item Servis, Pembayaran, Produk)</p>
            <p> <strong>Edit data</strong> langsung di Excel/Google Sheets sebelum restore</p>
            <p> <strong>Pastikan kolom ID</strong> tetap ada dan unik untuk menjaga integritas data</p>
            <p> <strong>Format tanggal</strong> direkomendasikan: YYYY-MM-DD HH:MM:SS</p>
            <p> <strong>Backup rutin</strong> sebelum melakukan perubahan besar pada data</p>
          </div>
        </section>
      </div>
    </div>
  );
}
