// Central place to define what to pre-sync for each route.
// Keep it lightweight and safe (fail silently). This runs on EVERY navigation.

function getToken() {
  try { return localStorage.getItem("token"); } catch { return null; }
}

function tinyDelay(ms = 120) { return new Promise((r) => setTimeout(r, ms)); }
function rafFrames(n = 2) { return new Promise((resolve) => { let i = 0; const step = () => { if (++i >= n) return resolve(); requestAnimationFrame(step); }; requestAnimationFrame(step); }); }

export async function syncForLocation(pathname, search) {
  const token = getToken();
  const isAuthed = Boolean(token);
  const { API } = await import("../../lib/api");

  const starts = (p) => pathname.startsWith(p);

  try {
    if (!isAuthed) return tinyDelay(100);

    if (pathname === "/") {
      // Light wait to show overlay minimal
      await rafFrames(2);
      return;
    }

    if (pathname === "/bon") {
      await API.prefetchDashboard({ force: true }); // ensure fresh data
      // Give the page one more frame to consume cache and render
      await rafFrames(2);
      return;
    }

    if (starts("/bon/customer/")) {
      const customerId = pathname.split("/bon/customer/")[1]?.split("/")[0];
      await API.prefetchDashboard({ force: true });
      if (customerId) await API.prefetchCustomerSummary(customerId, { force: true });
      await rafFrames(2);
      return;
    }

    if (pathname === "/kasir" || pathname === "/produk" || pathname === "/detail-pengguna" || pathname === "/detail-bengkel" || pathname === "/pengaturan" || pathname === "/tentang-kami") {
      await rafFrames(1);
      return;
    }

    await rafFrames(1);
    return;
  } catch (_) {
    return tinyDelay(100);
  }
}