import React from "react";
import { useLoading } from "../../hooks/use-loading";
import spinner from "../../assets/loading/spinner.svg";

export function LoadingOverlay() {
  const { isLoading } = useLoading();
  if (!isLoading) return null;
  return (
    <div
      data-testid="global-loading-overlay"
      style={{ position: "fixed", inset: 0, background: "rgba(255,255,255,0.6)", backdropFilter: "blur(8px)", WebkitBackdropFilter: "blur(8px)", display: "flex", alignItems: "center", justifyContent: "center", zIndex: 9999 }}
      aria-live="polite"
      aria-busy="true"
      role="status"
    >
      <div style={{ display: "flex", flexDirection: "column", alignItems: "center", gap: 12 }}>
        <img src={spinner} alt="Loading" width={64} height={64} />
        <div style={{ fontSize: 14, color: "#334155" }}>Memuat...</div>
      </div>
    </div>
  );
}