import React, { useEffect, useState, useContext, createContext } from "react";
import { useToast } from "../../hooks/use-toast";

const AuthContext = createContext(null);
export const useAuth = () => useContext(AuthContext);

export default function AuthProvider({ children }) {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const { toast } = useToast();

  useEffect(() => {
    const saved = localStorage.getItem("user");
    if (saved) setUser(JSON.parse(saved));
    setLoading(false);
  }, []);

  async function login(username, password) {
    try {
      const { API } = await import("../../lib/api");
      const { access_token, user } = await API.login(username, password);
      localStorage.setItem("token", access_token);
      localStorage.setItem("user", JSON.stringify(user));
      setUser(user);
      toast({ title: "Berhasil masuk", description: `Selamat datang ${user.username}` });
      return true;
    } catch (e) {
      const msg = e.response?.data?.detail;
      const desc = typeof msg === 'string' ? msg : (msg?.msg || msg?.message || JSON.stringify(msg));
      toast({ title: "Gagal masuk", description: desc || "Username atau password salah", variant: "destructive" });
      return false;
    }
  }

  async function register(userData) {
    try {
      const { API } = await import("../../lib/api");
      const { access_token, user } = await API.register(userData);
      localStorage.setItem("token", access_token);
      localStorage.setItem("user", JSON.stringify(user));
      setUser(user);
      toast({ title: "Berhasil mendaftar", description: `Selamat datang ${user.username}` });
      return true;
    } catch (e) {
      const msg = e.response?.data?.detail;
      const desc = typeof msg === 'string' ? msg : (msg?.msg || msg?.message || JSON.stringify(msg));
      toast({ title: "Gagal mendaftar", description: desc || "Terjadi kesalahan", variant: "destructive" });
      return false;
    }
  }

  function logout() {
    localStorage.removeItem("token");
    localStorage.removeItem("user");
    localStorage.removeItem("lastPage");
    setUser(null);
  }

  const value = { user, login, register, logout, loading };
  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
}