import React from "react";
import { BrowserRouter as Router, Routes, Route, Navigate } from "react-router-dom";
import { Toaster } from "../components/ui/toaster";
import { useAuth } from "./providers/auth-context";
import MenuDashboard from "../pages/MenuDashboard";
import Bon from "../pages/Bon";
import Cashier from "../pages/Cashier";
import Products from "../pages/Products";
import UserDetail from "../pages/UserDetail";
import WorkshopDetail from "../pages/WorkshopDetail";
import Settings from "../pages/Settings";
import About from "../pages/About";
import Auth from "../pages/Auth";
import Backup from "../pages/Backup";
import GlobalRouteEffects from "./navigation/GlobalRouteEffects";

export default function AppRouter() {
  const { user, loading } = useAuth();
  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-slate-50 to-blue-50">
        <div className="text-slate-700">Memuat...</div>
      </div>
    );
  }
  return (
    <Router>
      {/* Global route transition handler */}
      <GlobalRouteEffects />
      <Routes>
        <Route path="/" element={user ? <MenuDashboard /> : <Navigate to="/auth" replace />} />
        <Route path="/auth" element={<Auth />} />
        {/* Bon */}
        <Route path="/bon" element={user ? <Bon /> : <Navigate to="/auth" replace />} />
        <Route path="/bon/customer/:customerId" element={user ? <Bon /> : <Navigate to="/auth" replace />} />
        {/* Menu lain */}
        <Route path="/kasir" element={user ? <Cashier /> : <Navigate to="/auth" replace />} />
        <Route path="/produk" element={user ? <Products /> : <Navigate to="/auth" replace />} />
        <Route path="/backup" element={user ? <Backup /> : <Navigate to="/auth" replace />} />
        <Route path="/detail-pengguna" element={user ? <UserDetail /> : <Navigate to="/auth" replace />} />
        <Route path="/detail-bengkel" element={user ? <WorkshopDetail /> : <Navigate to="/auth" replace />} />
        <Route path="/pengaturan" element={user ? <Settings /> : <Navigate to="/auth" replace />} />
        <Route path="/tentang-kami" element={user ? <About /> : <Navigate to="/auth" replace />} />
        {/* Redirect default */}
        <Route path="*" element={<Navigate to="/" replace />} />
      </Routes>
      <Toaster />
    </Router>
  );
}