import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Button } from "../components/ui/button";
import { Input } from "../components/ui/input";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "../components/ui/card";
import { Label } from "../components/ui/label";
import { Wrench, ArrowRight, RefreshCw } from "lucide-react";
import { useAuth } from "../app/providers/auth-context";

export default function Auth() {
  const navigate = useNavigate();
  const { user, login, register } = useAuth();
  const [isLogin, setIsLogin] = useState(true);
  const [isLoading, setIsLoading] = useState(false);
  const [formData, setFormData] = useState({ username: "", password: "", email: "", workshop_name: "", workshop_id: "", role: "owner" });

  useEffect(() => { if (user) navigate("/", { replace: true }); }, [user, navigate]);

  async function handleSubmit(e) {
    e.preventDefault(); setIsLoading(true);
    const ok = isLogin ? await login(formData.username, formData.password) : await register(formData);
    setIsLoading(false);
    if (ok) navigate("/", { replace: true });
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 via-indigo-50 to-purple-50 flex items-center justify-center p-4 relative overflow-hidden">
      <div className="absolute inset-0 overflow-hidden">
        <div className="absolute -top-40 -right-40 w-80 h-80 bg-gradient-to-br from-blue-400/20 to-purple-400/20 rounded-full blur-3xl" />
        <div className="absolute -bottom-40 -left-40 w-80 h-80 bg-gradient-to-br from-indigo-400/20 to-pink-400/20 rounded-full blur-3xl" />
      </div>
      <Card className="w-full max-w-md backdrop-blur-sm bg-white/90 shadow-2xl border-0 relative z-10">
        <CardHeader className="text-center pb-8">
          <div className="mx-auto mb-6 w-20 h-20 bg-gradient-to-br from-blue-600 to-indigo-600 rounded-2xl flex items-center justify-center shadow-lg transform rotate-3">
            <Wrench className="w-10 h-10 text-white" />
          </div>
          <CardTitle className="text-3xl font-bold bg-gradient-to-r from-blue-600 to-indigo-600 bg-clip-text text-transparent">{isLogin ? "Masuk ke Sistem" : "Daftar Bengkel Baru"}</CardTitle>
          <CardDescription className="text-base text-slate-600">{isLogin ? "Kelola bengkel Anda dengan mudah" : "Mulai digitalisasi bengkel Anda"}</CardDescription>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit} className="space-y-5">
            <div className="space-y-2"><Label htmlFor="username">Username</Label><Input id="username" name="username" value={formData.username} onChange={(e) => setFormData({ ...formData, username: e.target.value })} required /></div>
            <div className="space-y-2"><Label htmlFor="password">Password</Label><Input id="password" name="password" type="password" value={formData.password} onChange={(e) => setFormData({ ...formData, password: e.target.value })} required /></div>
            {!isLogin && (
              <>
                <div className="space-y-2">
                  <Label htmlFor="role">Saya adalah</Label>
                  <select id="role" name="role" className="w-full h-11 p-3 border border-slate-200 rounded-lg" value={formData.role} onChange={(e) => setFormData({ ...formData, role: e.target.value })} required>
                    <option value="owner">Pemilik Bengkel</option>
                    <option value="employee">Karyawan Bengkel</option>
                  </select>
                </div>
                {formData.role === "employee" && (
                  <div className="space-y-2"><Label htmlFor="workshop_id">ID Bengkel</Label><Input id="workshop_id" name="workshop_id" value={formData.workshop_id} onChange={(e) => setFormData({ ...formData, workshop_id: e.target.value.toUpperCase() })} maxLength={18} className="h-11" required /></div>
                )}
                {formData.role === "owner" && (
                  <div className="space-y-2"><Label htmlFor="workshop_name">Nama Bengkel</Label><Input id="workshop_name" name="workshop_name" value={formData.workshop_name} onChange={(e) => setFormData({ ...formData, workshop_name: e.target.value })} className="h-11" required /></div>
                )}
                <div className="space-y-2"><Label htmlFor="email">Email (Opsional)</Label><Input id="email" name="email" type="email" value={formData.email} onChange={(e) => setFormData({ ...formData, email: e.target.value })} className="h-11" placeholder="Boleh dikosongkan" /></div>
              </>
            )}
            <Button type="submit" className="w-full h-12 bg-gradient-to-r from-blue-600 to-indigo-600 hover:from-blue-700 hover:to-indigo-700 text-white" disabled={isLoading}>{isLoading ? (<span className="flex items-center"><RefreshCw className="w-4 h-4 mr-2 animate-spin" />{isLogin ? "Masuk..." : "Mendaftar..."}</span>) : (<span className="flex items-center">{isLogin ? "Masuk" : "Daftar"}<ArrowRight className="w-4 h-4 ml-2" /></span>)}</Button>
            <Button type="button" variant="ghost" className="w-full h-11 text-slate-600 hover:text-slate-800" onClick={() => setIsLogin(!isLogin)}>{isLogin ? "Belum punya akun? Daftar di sini" : "Sudah punya akun? Masuk di sini"}</Button>
          </form>
        </CardContent>
      </Card>
    </div>
  );
}