import axios from "axios";

const BACKEND_URL = process.env.REACT_APP_BACKEND_URL;
const API_BASE = `${BACKEND_URL}/api`;

// Simple in-memory cache to ensure pages can render with data immediately after prefetch
const Cache = {
  dashboard: null,
  customerSummaries: new Map(), // key: customerId -> summary
  ts: {
    dashboard: 0,
  },
};

export const CacheReader = {
  getDashboard() { return Cache.dashboard; },
  getCustomerSummary(id) { return Cache.customerSummaries.get(id) || null; },
};

function dispatch(name, detail) {
  try { window.dispatchEvent(new CustomEvent(name, { detail })); } catch {}
}

// moved: API._raw didefinisikan di dalam objek API di bagian bawah file

const api = axios.create({ baseURL: API_BASE });

api.interceptors.request.use((config) => {
  const token = localStorage.getItem("token");
  if (token) config.headers.Authorization = `Bearer ${token}`;
  return config;
});

api.interceptors.response.use(
  (r) => r,
  (error) => {
    if (error?.response?.status === 401) {
      localStorage.removeItem("token");
      localStorage.removeItem("user");
    }
    return Promise.reject(error);
  }
);

async function rafFrames(n = 1) {
  return new Promise((resolve) => {
    let i = 0;
    const step = () => {
      if (++i >= n) return resolve();
      requestAnimationFrame(step);
    };
    requestAnimationFrame(step);
  });
}

export const API = {
  // ---------- AUTH ----------
  async login(username, password) {
    const { data } = await api.post("/auth/login", { username, password });
    return data;
  },
  async register(userData) {
    const { data } = await api.post("/auth/register", userData);
    return data;
  },
  async getCurrentUser() {
    const { data } = await api.get("/auth/me");
    return data;
  },

  // ---------- DASHBOARD (with cache) ----------
  async getDashboard({ preferCache = true } = {}) {
    if (preferCache && Cache.dashboard) {
      // Signal that UI can use cached data immediately
      dispatch("cache:dashboard:served", { from: "cache" });
      return Cache.dashboard;
    }
    const { data } = await api.get("/dashboard");
    Cache.dashboard = data;
    Cache.ts.dashboard = Date.now();
    dispatch("cache:dashboard:updated", { ts: Cache.ts.dashboard });
    return data;
  },
  async prefetchDashboard({ force = false } = {}) {
    if (!force && Cache.dashboard) return Cache.dashboard;
    try {
      const { data } = await api.get("/dashboard");
      Cache.dashboard = data;
      Cache.ts.dashboard = Date.now();
      dispatch("cache:dashboard:updated", { ts: Cache.ts.dashboard });
      // Wait a couple of frames so components mounting can consume cache synchronously
      await rafFrames(2);
      return data;
    } catch (e) {
      // don't throw in prefetch; page can still fetch
      return null;
    }
  },
  invalidateDashboard() {
    Cache.dashboard = null;
    dispatch("cache:dashboard:invalidated");
  },

  // ---------- CUSTOMERS ----------
  async addCustomer(customerData) {
    const { data } = await api.post("/customers", customerData);
    // Invalidate dashboard cache to reflect new customer
    API.invalidateDashboard();
    return data;
  },
  async getCustomerSummary(customerId, { preferCache = true } = {}) {
    if (preferCache && Cache.customerSummaries.has(customerId)) {
      const val = Cache.customerSummaries.get(customerId);
      dispatch("cache:summary:served", { customerId, from: "cache" });
      return val;
    }
    const { data } = await api.get(`/customers/${customerId}/summary`);
    Cache.customerSummaries.set(customerId, data);
    dispatch("cache:summary:updated", { customerId });
    return data;
  },
  async prefetchCustomerSummary(customerId, { force = false } = {}) {
    if (!force && Cache.customerSummaries.has(customerId)) return Cache.customerSummaries.get(customerId);
    try {
      const { data } = await api.get(`/customers/${customerId}/summary`);
      Cache.customerSummaries.set(customerId, data);
      dispatch("cache:summary:updated", { customerId });
      await rafFrames(2);
      return data;
    } catch {
      return null;
    }
  },
  invalidateCustomerSummary(customerId) {
    if (customerId) {
      Cache.customerSummaries.delete(customerId);
      dispatch("cache:summary:invalidated", { customerId });
    } else {
      Cache.customerSummaries.clear();
      dispatch("cache:summary:invalidated", { all: true });
    }
  },
  async deleteCustomer(customerId) {
    const { data } = await api.delete(`/customers/${customerId}`);
    API.invalidateCustomerSummary(customerId);
    API.invalidateDashboard();
    return data;
  },

  // ---------- SERVICE SESSIONS ----------
  async addServiceSession(sessionData) {
    const { data } = await api.post("/service-sessions", sessionData);
    API.invalidateCustomerSummary(sessionData.customer_id);
    API.invalidateDashboard();
    return data;
  },
  async deleteServiceSession(sessionId) {
    const { data } = await api.delete(`/service-sessions/${sessionId}`);
    // We don't know customerId here; ensure summaries are refreshed globally
    API.invalidateCustomerSummary();
    API.invalidateDashboard();
    return data;
  },

  // ---------- SERVICES ----------
  async addService(serviceData) {
    const { data } = await api.post("/services", serviceData);
    API.invalidateCustomerSummary(serviceData.customer_id);
    API.invalidateDashboard();
    return data;
  },
  async updateService(serviceId, serviceData) {
    const { data } = await api.put(`/services/${serviceId}`, serviceData);
    // We may not know the customer here; invalidate summaries globally to avoid stale UI
    API.invalidateCustomerSummary();
    API.invalidateDashboard();
    return data;
  },
  async deleteService(serviceId) {
    const { data } = await api.delete(`/services/${serviceId}`);
    API.invalidateCustomerSummary();
    API.invalidateDashboard();
    return data;
  },

  // ---------- PAYMENTS ----------
  async addPayment(paymentData) {
    const { data } = await api.post("/payments", paymentData);
    API.invalidateCustomerSummary(paymentData.customer_id);
    API.invalidateDashboard();
    return data;
  },
  async updatePayment(paymentId, paymentData) {
    const { data } = await api.put(`/payments/${paymentId}`, paymentData);
    // Unknown customer here: clear summaries to force fresh fetch next time
    API.invalidateCustomerSummary();
    API.invalidateDashboard();
    return data;
  },
  async deletePayment(paymentId) {
    const { data } = await api.delete(`/payments/${paymentId}`);
    API.invalidateCustomerSummary();
    API.invalidateDashboard();
    return data;
  },
  async getPaymentAudits(paymentId) {
    const { data } = await api.get(`/payments/${paymentId}/audits`);
    return data;
  },

  // ---------- PRODUCTS ----------
  async listProducts() {
    const { data } = await api.get(`/products`);
    return data;
  },
  async createProduct(p) {
    const { data } = await api.post(`/products`, p);
    return data;
  },
  async updateProduct(id, p) {
    const { data } = await api.put(`/products/${id}`, p);
    return data;
  },
  async deleteProduct(id) {
    const { data } = await api.delete(`/products/${id}`);
    return data;
  },
  async adjustStock(id, delta) {
    const { data } = await api.post(`/products/${id}/adjust-stock`, { delta });
    return data;
  },

  // ---------- RAW (untuk backup UI) ----------
  _raw: {
    async get(path) { return api.get(path); },
    async post(path, payload) { return api.post(path, payload); },
  },



  // ---------- WhatsApp helper ----------
  async getWhatsAppMessage(customerId, sessionId = null) {
    const params = sessionId ? `?session_id=${sessionId}` : "";
    const { data } = await api.get(`/customers/${customerId}/whatsapp-message${params}`);
    return data;
  },
};